
.news-row {
    transition: all 0.5s ease;
    padding: 0 $half-margin;

    h4 {
        padding: $half-margin 0 !important;
    }

    &.not-archive .news-col {padding :0 $half-margin; }
    .news-col {
        //transition: all 0.5s ease;
        padding: $half-margin;
        margin: 0;
        z-index: 0!important;
        position: relative;

        &.filtr-item {
            background: none !important;
            transition: none;

            .image-link {
                display: block;
            }
        }
        .row {
            width: 100%;
            height: 100%;
            align-items: center;
            align-content: flex-start;
            //padding-top: $base-margin;
            padding-bottom: $base-margin;
        }
        div {
            background: $white;
        }
        h4 {
            a:hover {
                text-decoration: underline;
            }
            margin-top: 0;
        }
        .thumb-image {
            display: block;
            width: 100%;
            height: auto;
        }
        .news-col-text {
            padding-top: $base-margin !important;
            .row {
                border-bottom: 1px solid color('grey', 'lighten-2');
                &>div:first-child {
                    border-right: 1px solid color('grey', 'lighten-2');
                }
            }
        }
        &.dark-grey {
            div {
                background: color('grey', 'base');
            }
            h3,
            h2 {
                margin: 0;
                color: $white;
            }
            p {
                color: $white;
            }
        }
        .news-col-text {
            .row {
                padding-top: 0;
            }
        }
        .news-col-image {
            a {
                display: block;
                margin: 0; padding: 0;
                height: auto;
                overflow: hidden;
            }
            img {
                transition: transform 0.2s ease;
            }
        }
    }
    .news-col:hover .news-col-image img {
        transform: scale(1.2);
    }


    &.filtr-container {
        padding: 0;
        display: block !important;

        .filtr-item {
            height: auto;
        }
    }
}



.deposit-link-block {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: color("essblue", "lighten-2");
    transition: background-color 0.5s;
    padding: $base-margin;
    padding-top: $base-margin;

    &:hover {
        text-decoration: none;
        background-color: $primary-color;
    }
    .big-icon {
        font-size: $h1;
        color: $secondary-color;
        line-height: $h1;
    }
    .claim {
        font-size: $h4;
        line-height: $h3;
        color: $white;
    }
}
#ess-body .ess-posts-filter .container {
    ul {
        background: none;
        max-width: 100%;
        li {
            list-style: none;
        }
        &.tabs {
            .indicator {
                bottom: -20px;
            }
        }
    }
}
#ess-body .container article {
    padding: $double-margin 0 $quad-margin 0;

    h1 {
        margin: $double-margin 0;
    }
    h4 {

    }
    ul { max-width: 100% }
    ul li {
        font-size: 90%;
        max-width: auto;
    }
    p {
        font-size: 90%;
        padding: $quarter-margin 0;
    }
}
article figure {
    text-align: center;
}

.social-line {
    margin-top: $base-margin;
    border-top: 1px solid color('grey', 'lighten-2');
    ul {
        text-align: right;
        li {
            display: inline-block;
            &::before {
				display: none;
			}
			a {
                text-decoration: none;
                cursor: pointer;
                &:hover {
                    text-decoration: none;
                    color: $primary-color;
                }
                color: $secondary-color;
                transition: color 0.5s ease ;
            }
            .ess-icon {
                font-size: 250%;
            }
        }
    }
}

@media only screen and (max-width: $medium-screen) {
    .news-col {
        .no-margin {
            padding: $base-margin 0;
        }
        .news-col-text {
            padding-left: $base-margin !important;
            p {
                display: none;
            }
        }
    }
}

@media only screen and (max-width: $small-screen) {
    .news-col {
        border-bottom: 1px solid color("grey", "lighten-2");
    }
    .deposit-link-block {
        margin-top: $base-margin;
    }
}